@import "../../vars.scss";

.travel {
    margin: 50px auto;
    max-width: 800px;

    h1 {
        font-size: 50px;
        margin: 0 0 60px;
    }

    h2 {
        font-size: 36px;
        margin: 20px;
        letter-spacing: 4px;
    }

    p {
        font-family: $font-secondary;
        font-size: 18px;
        line-height: 26px;
    }

    a {
        color: gold;
        
        &:visited {
            color: gold;
        }

        &:hover, &:focus {
            text-decoration: none;
        }
    }

    li {
       list-style: none;
    }

    &__category-name {
        font-size: 26px;
        font-family: $font-secondary;
        text-transform: uppercase;
        display: inline-block;
        border-bottom: solid 1px $color-primary;
        margin: 30px 0 10px;
    }

    h3 {
        font-family: $font-secondary;
        font-size: 18px;
    }

    &__suggestion {
        margin-bottom: 10px;
    }
}